/*
    DEMO STYLE
*/


p {
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
    color: #999;
}

a,
a:hover,
a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
}

.navbar {
    background: #e2e2e2;
    border: none;
    border-radius: 0;
    margin-bottom: 40px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
    max-width: 10vw;
    position: fixed;
}

.navbar-btn {
    box-shadow: none;
    outline: none !important;
    border: none;
}

.line {
    width: 100%;
    height: 1px;
    border-bottom: 1px dashed #ddd;
    margin: 40px 0;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

.sidebarHeader {
    font-family: 'Cormorant Upright', serif;
    font-size: 20px;
    /* margin-left: 2.3vw; */
    text-align: center;
}

.appNamesidebar2 {
    font-style: italic;
    color: #90b1ad;
    text-align: center;
}

.rescueName {
    margin-top: -18px;
    font-size: 25px;
    text-align: center;
}


#sidebar {
    min-width: 16vw;
    max-width: 12vw;
    background: #1f2628;
    color: #e2e2e2;
    transition: all 0.3s;
    min-height: 100vh;
}

#sidebar.active {
    margin-left: -250px;
}

#sidebar .sidebarHeader {
    padding: 20px;
    background: #1f2628;
}

#sidebar ul.components {
    padding: 0;
    
}

#ul.components {
    margin-left: 20px;
}

#sidebar ul p {
    color: #e2e2e2;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.0em;
    display: block;
}

#sidebar ul li a:hover {
    color: #ced7da;
    background: #355f64;
}

#sidebar ul li.active>a,
a[aria-expanded="true"] {
    color: #ced7da;
    background: #355f64;
}

a[data-toggle="collapse"] {
    position: relative;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

ul ul {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #1f2628;
}

ul.CTAs {
    padding: 20px;
}

ul.CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px;
}

a.download {
    background: #ced7da;
    color: #1f2628;
}

a.article,
a.article:hover {
    background: #6d7fcc !important;
    color: #ced7da !important;
}

